<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Payment</h1>
       <div class="chartWrap">
        </div>
    </section>
    <div class="center">
        <div class="price">
            {{orderDate.grandTotal}} AED
        </div>
        <div class="payMethod">
            <div class="codWrap">
                <span class="cod"> <van-radio-group v-model="radio"><van-radio name="1" checked-color="#e33946">COD</van-radio></van-radio-group></span>
                <span class="additional">+50 AED <van-icon name="question-o" color="#55a4e1" size="17px"/></span>
            </div>
            <div class="cardWrap" @click='redirect("/addBankcard")'>
                <span class="cod"> <van-radio-group v-model="radio"><van-radio name="2" checked-color="#e33946"> Bank Card</van-radio></van-radio-group></span>
                <span class="bankCard">8187************7848 <van-icon @click="show = true" name="arrow-down" size="17px"/></span>
            </div>
             <div class="paypalWrap">
                <span class="cod"> <van-radio-group v-model="radio"><van-radio name="3" checked-color="#e33946"> Paypal</van-radio></van-radio-group></span>
            </div>
        </div>
        <van-button type="danger">Pay</van-button>
    </div>
    <van-popup v-model="show" position="bottom">
        <div class="newCard">
            <span @click="show = false">Cancel</span>
            <span class="add" @click="show = false">Add New Card</span>
        </div>
        <div class="choiceBankWrap">
                <div class="choiceBank">
                    <div class="btn_left">
                        <van-radio-group v-model="radio2">
                        <van-radio name="1" checked-color="#e33946"></van-radio>
                        </van-radio-group>
                    </div>
                    <div class="bankName">
                        <p>xxxxxxxxxxxxxxBank Name</p>
                        <span>8187************7848</span>
                    </div>
                </div>
                <div class="choiceBank">
                    <div class="btn_left">
                        <van-radio-group v-model="radio2">
                        <van-radio name="2" checked-color="#e33946"></van-radio>
                        </van-radio-group>
                    </div>
                    <div class="bankName">
                        <p>xxxxxxxxxxxxxxBank Name</p>
                        <span>8187************7848</span>
                    </div>
                </div>
                   
                
        </div>
    </van-popup>
</div>
</template>

<script>

import {submitOrder} from '@/api/order.js'
export default {
  name: 'payMent',
  data () {
    return {
        radio: '1',
        show: false,
        radio2: '1',
        orderDate:{}
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
      
    init(){
        let ids=this.$route.query.ids;
        //console.log(JSON.parse(ids).join(','))
        let opt={
           grandTotal:this.$route.query.grandTotal,
            addressId:this.$route.query.addressId,
            ids:JSON.parse(ids).join(','),
            couponId:this.$route.query.couponId
        }
       submitOrder(opt) .then(res=>{
           this.orderDate=res.data.result
       })
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
    .center{
        width: 100%;
        border-top: 10px #f6f6f6 solid;
        .price{
            color: #e33946;
            display: flex; align-items: center; justify-content: center;
            font-size: 19px;
            padding: 25px 4%;
            border-bottom: #f6f6f6 1px solid;
        }
        .payMethod{
            width: 100%;
            padding-left: 4%;
            .codWrap,.cardWrap,.paypalWrap{
                width: 100%;
                display: -webkit-box;
                padding: 15px 4%;
                border-bottom: 1px solid #f6f6f6;
                .additional{
                    font-size: 12px;
                    display: flex; align-items: center; justify-content: flex-end;
                    .van-icon{
                        margin-left: 10px;
                    }
                }
                .bankCard{
                    font-size: 12px;
                    display: flex; align-items: center; justify-content: flex-end;
                    color: #999999;
                    .van-icon{
                        margin-left: 10px;
                    }
                }
                .cod{
                    font-size: 14px;
                }
                span{
                    display: block;
                    width: 50%;
                   
                }
            }
        }
        .van-button--danger{
            width: 92%;
            margin-left: 4%;
            margin-top: 70px;
        }
    }
    .newCard{
        padding: 20px 4% 0px;
        display: -webkit-box;
        span{
            color: #999999;
            font-size: 15px;
            display: block;
            width: 50%;
        }
        .add{
            color: #e33946;
            text-align: right;
        }
    }
    .choiceBankWrap{
        padding: 20px 0 20px 4%;
        width: 100%;
        .choiceBank{
            display: -webkit-box;
            width: 100%;
            .btn_left{
                display: flex; align-items: center;
                padding-right: 15px;
            }
            .bankName{
                padding: 10px 0 15px 0;
                p{
                    font-size: 15px;
                    padding-bottom: 10px;
                }
                span{
                    color: #666666;
                    font-size: 14px;
                }
            }
        }
    }
    .choiceBank:first-child{
        border-bottom: 1px solid #f6f6f6;
    }
</style>



